.chart-bar {
height: 100%;
display: flex;
flex-direction: column;
align-items: center;
}



.chart-bar__container {
    height: 100%;
width: 100%;
border: 1px solid #313131;
border-radius: 12px;
background-color: #c3b4f3;
overflow: hidden;
display: flex;
flex-direction: column;
justify-content: flex-end;
}
.char-bar__column {
background-color: #4826b9;
width: 100%;
transition: all 0.3s ease-out;
}
.char-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
}